.servcont {
  height: 800px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}

.container-ind {
  color: whitesmoke;
  border-radius: 0.5rem;
  border: 1px solid whitesmoke;
  display: flex;
  justify-content: space-around;
  margin: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 90%;
}

.mixCont {
  flex-direction: row-reverse;
}

.aloneCont {
  flex-direction: row;
}

.ContInner {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  width: 50%;
  min-width: 50%;
  text-align: left;
  font-size: 0.8rem;
  height: 100%;
}

.imgExpedition {
  height: 100%;
  padding: 0.2rem;
  max-width: 100%;
}

@media only screen and (max-width: 660px) {
  .ContInner {
    width: 90%;
    height: 10rem;
  }
  .imgExpedition {
    display: none;
  }
}
