.footer {
  height: 3rem;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.logo {
  color: whitesmoke;
  font-size: 1rem;
}

.footBar {
  display: flex;
  justify-content: space-around;
  text-align: center;
  flex-basis: 40%;
}

.linkF {
  font-size: 1rem;
  color: whitesmoke;
  text-decoration: none;
  transition: 1s;
  padding: 0.2rem;
  border-radius: 0.2rem;
}

.linkF:hover {
  background-color: whitesmoke;
  color: rgb(234, 157, 34);
}

@media only screen and (max-width: 768px) {
  .footBar {
    flex-basis: 60%;
  }
}

@media only screen and (max-width: 480px) {
  .logo {
    color: whitesmoke;
    font-size: 0.7rem;
  }
  .footBar {
    flex-basis: 60%;
  }

  .linkF {
    font-size: 0.7rem;
  }
}
