.header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  color: whitesmoke;
  flex-wrap: wrap;
  height: 2.5rem;
  flex-grow: 1;
}

.header h1 {
  flex-basis: 10%;
  text-align: center;
  font-size: 1rem;
}

.navBar {
  flex-basis: 50%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  list-style: none;
}

.link {
  border-radius: 0.5rem;
  font-size: 1rem;
  text-decoration: none;
  padding: 0.2rem;
  flex-basis: auto;
  transition: 1s;
}

/* unvisited link */
.link:link {
  color: whitesmoke;
}

/* visited link */
.link:visited {
  color: whitesmoke;
}

/* mouse over link */
.link:hover {
  background-color: whitesmoke;
  color: rgb(234, 157, 34);
}

/* selected link */
.link:active {
  color: rgb(234, 157, 34);
}

.line {
  background-color: whitesmoke;
  height: 0.3rem;
  margin: 0.2rem 0;
  width: 1.5rem;
  display: none;
}

.mobileMenu {
  position: absolute;
  display: flex;
  justify-content: space-around;
  top: 0;
  opacity: 0.95;
  background-color: whitesmoke;
  width: 100%;
  height: 20rem;
  text-align: center;
  z-index: 5;
}

.navBarMobile {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-basis: 70%;
}

.navBarMobile > button {
  border: 1px solid rgb(234, 157, 34);
  border-radius: 0.1rem;
  padding: 0rem 0.5rem;
}

.link-mobile {
  font-size: 1rem;
  text-decoration: none;
  color: rgb(234, 157, 34);
}

@media only screen and (max-width: 480px) {
  .header {
    justify-content: space-between;
  }
  .header h1 {
    text-align: center;
    flex-basis: 20%;
  }
  .navBar {
    display: none;
  }

  .container-burger {
    flex-basis: 1%;
    margin-right: 1.5rem;
  }

  .line {
    display: block;
  }

  .link {
    text-align: center;
  }
}
