.usCon {
  height: 800px;
  display: flex;
  align-items: center;
  gap: 5rem;
  justify-content: center;
}

.missionCont {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  flex-basis: 15rem;
  text-align: center;
  border: 3px solid whitesmoke;
  color: whitesmoke;
  min-height: 350px;
  margin: 0.5rem;
  padding: 0.5rem;
  border-radius: 1rem;
  transition: 1s;
}

.missionCont h1 {
  font-size: 1.2rem;
}

.missionCont p {
  font-size: 1rem;
}

.missionCont:hover {
  background-color: whitesmoke;
  color: rgb(234, 157, 34);
  border: 3px solid rgb(255, 157, 0);
  transform: translateY(-10px);
}

@media only screen and (max-width: 768px) {
  .usCon {
    flex-wrap: wrap;
    align-content: center;
    height: 1000px;
  }
  .missionCont {
    min-height: 200px;
    flex-basis: 60%;
    margin: 0.5rem 0;
  }

  .missionCont h1,
  .missionCont p {
    font-size: 1rem;
  }
}
