.home {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 100vh;
}

.titleCont {
  width: 50%;
  padding-left: 15%;
}

.titleCont h1,
.titleCont p {
  color: whitesmoke;
}

.titleCont h1 {
  font-size: 2rem;
}

.titleCont p {
  font-size: 1rem;
}

.titleCont a:link,
.titleCont a:visited {
  text-decoration: none;
  color: whitesmoke;
  font-size: 1rem;
  border: 1px solid whitesmoke;
  padding: 0.1rem 0.3rem;
  border-radius: 0.3rem;
  transition: 1s;
}

.linkHome:hover {
  background-color: whitesmoke;
}

.titleCont > .containerButtom > a:hover {
  color: rgb(234, 157, 34);
}

.titleCont a:active {
  color: whitesmoke;
  background-color: rgb(234, 157, 34);
}

.imgCont {
  height: auto;
  width: 50%;
}

.imgCont img {
  height: 18rem;
  max-width: 100%;
  border-radius: 1rem;
}

@media only screen and (max-width: 780px) {
  .home {
    flex-wrap: wrap;
    align-content: center;
    gap: 1rem;
  }
  .titleCont {
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-basis: 100%;
    height: 8rem;
    padding-left: 0;
    align-items: center;
  }

  .imgCont {
    height: auto;
    width: 70%;
  }
}

@media only screen and (max-width: 475px) {
  .titleCont {
    width: 90%;
    text-align: center;
  }

  .titleCont h1 {
    font-size: 1.7rem;
  }

  .titleCont p {
    font-size: 0.8rem;
  }
}
